.card {
  display: flex;
  gap: 16px;
}

.cardAvatar {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
  justify-content: center;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-align: left;
  height: 228px;
  padding: 32px;
  padding-bottom: 0;
}

.cardAvatar img {
  border-radius: 50%;
  height: 80px;
  width: 80px;
}

.schoolType {
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: center;
}

.schoolName {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.25px;
  text-align: right;
}

.cardDescription {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-align: left;
  height: 228px;
  padding: 32px;
  padding-bottom: 0;
}
